@use "../abstracts/variables";
@use "../abstracts/mixins";

.faq {
  margin: variables.$margin-xxl auto 32px;
  width: 100%;

  @include mixins.media("tablet-l") {
    margin: variables.$margin-section auto 48px;
  }

  &__description {
    @include mixins.media("tablet-l") {
      font-size: variables.$font-size--general-l;
    }
  }

  &__title,
  &__description {
    margin-bottom: variables.$margin-l;
    text-align: center;
    color: variables.$color-black;
  }

  &__title span,
  .accordion-item__button {
    text-transform: uppercase;
  }

  &__title span {
    display: block;
  }

  &__title,
  &__title span {
    font-size: variables.$font-size--mobile--h1-customer;
    line-height: variables.$line-height--mobile--h1-customer;

    @include mixins.media("tablet-l") {
      font-size: variables.$font-size--mobile-h1;
      line-height: variables.$line-height--mobile-h1;
    }
  }

  .account-sidebar li:first-child {
    padding-top: 8px;
  }
  .account-sidebar li:last-child {
    padding-bottom: 8px;
  }

  .account-sidebar__mobile .custom-select__items {
    height: auto;
    max-height: 900px;
    transition: max-height 0.3s ease-in-out, padding 0.3s ease-in-out;
  }

  .account-sidebar__mobile .account-sidebar__options[data-active="false"] .custom-select__items {
    height: auto;
    max-height: 0;
  }

  .accordions {
    margin-bottom: 0;
  }

  .accordion-item__content {
    color: variables.$color-black;
  }

  .accordion-item__button[data-active="true"] ~ div.accordion-item__content {
    padding: 18px 16px;
  }

  .account-sidebar {
    margin: 0 0 32px !important;

    @include mixins.media("tablet-l") {
      margin: 0 24px 0 0 !important;
    }
  }

  .link-vanilla span {
    margin: 0;
  }
}

.faq-page {
  @include mixins.boxFlex(space-between, stretch, column);

  @include mixins.media("tablet-l") {
    @include mixins.boxFlex(space-between, stretch, row);
    flex-wrap: nowrap;
  }

  @include mixins.media("desktop-s") {
    padding: 0;
  }
}

.faq-menu {
  @include mixins.media("tablet-l") {
    width: 252px;
  }
}

.faq-content {
  width: 100%;
}

.faq + .box-customize {
  margin-bottom: 32px;

  @include mixins.media("tablet-l") {
    margin-bottom: 48px;
  }
}

.breadcrumbs-section {
  margin-bottom: 10px;

  @include mixins.media("tablet-l") {
    margin-bottom: 30px;
  }
}
