@use "../abstracts/variables";
@use "../abstracts/mixins";

$transition-config: 0.3s ease-in-out;

.account-sidebar {
  width: 100%;
  height: fit-content;
  margin: 0 0 variables.$margin-l !important;
  padding: 0;
  border-radius: 4px;
  background-color: variables.$color-principal;
  @include mixins.media("tablet-l") {
    position: sticky;
    top: 120px;
    margin: 0 variables.$margin-l 0 0 !important;
    padding: 12px 0;
    width: 25.5%;
  }

  a:visited {
    color: variables.$color-white;
  }

  &__mobile {
    display: block;

    @include mixins.media("tablet-l") {
      display: none;
    }

    .custom-select__selected,
    .custom-select__items li {
      text-transform: uppercase;
      cursor: pointer;
    }

    .custom-select__selected {
      padding: 16px;
      font-weight: variables.$font-semibold;
      font-size: variables.$font-size--general-up-m;
      color: variables.$color-white;
      @include mixins.boxFlex(space-between, center, row);

      span {
        max-width: calc(100% - 13px);
        @include mixins.excerptForLine(1);
      }
    }

    .custom-select__items {
      overflow: hidden;
      height: 267px;
      padding: 0 0 16px;
      transition: height $transition-config, padding $transition-config;
    }

    .custom-select__items li {
      padding: 8px 16px;
    }

    .icon-caret {
      width: 13px;
      transform: rotate(-90deg);
      transition: transform $transition-config;
    }

    .account-sidebar__options[data-active="false"] {
      .custom-select__items {
        height: 0;
        padding: 0;
      }
      .icon-caret {
        transform: rotate(0);
      }
    }
  }

  &__desktop {
    display: none;

    @include mixins.media("tablet-l") {
      display: block;
    }
  }

  ul {
    list-style: none;
  }

  li {
    margin: 0;
    padding: 8px 24px;
    color: variables.$color-white;
    transition: background-color $transition-config;
    cursor: pointer;

    &:first-child {
      padding-top: 12px;
    }

    &:last-child {
      padding-bottom: 12px;
    }

    &.active a {
      .icon-arrow {
        width: 14px;
      }

      span {
        max-width: 190px;
      }
    }
  }

  li:hover a,
  li.active a {
    text-shadow: -0.4px -0.4px 0 variables.$color-white, 0.4px 0.4px variables.$color-white;
    transition: text-shadow 0.3s ease-in-out;
  }

  a {
    display: inline-block;
    width: 100%;
    justify-content: space-between;
    font-weight: variables.$font-light;
    font-size: variables.$font-size--general-up-m;
    line-height: variables.$line-height--general-up-s;
    text-transform: uppercase;
    text-decoration: none;
    color: variables.$color-white;
    transition: color $transition-config, font-weight $transition-config, font-size $transition-config;

    @include mixins.media("tablet-l") {
      display: inline-flex;
    }

    .icon-arrow {
      width: 0;
      transition: width $transition-config;
    }

    .icon-arrow path {
      transition: fill $transition-config;
    }

    .animation {
      display: none;
    }

    span {
      margin-left: 0;
      overflow: hidden;
      width: fit-content;
      text-overflow: ellipsis;
      white-space: nowrap;

      &::after {
        display: none;
      }
    }
  }
}
